@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald:400,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root {
    min-height: 100%;
}

body {
    background-color: #121215;
    -webkit-font-smoothing: antialiased !important;
    font-family: 'Roboto', sans-serif;
}

.page {
    display: flex;
    height: 100vh;
    width: 80%;
    color: #dfdfea;
    padding-top: 20vh;
    margin: auto;
}

.title {
    border-bottom: 2px solid #aac;
    margin-bottom: 10px;
    padding: 10px 0;
}

.title h2 {
    color: #e55;
    font-weight: 700;
}

p {
  margin-top: 10px;  
}
header {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.logo {
    margin-left: 30px;
    margin-top: 30px;
    cursor: pointer; 
}

ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    margin-right: 20px;
}

.element {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 30px 40px 30px;
    font-weight: bold;
    border-bottom: 2px solid transparent;
    cursor: pointer;
    transition: border-bottom 0.2s;
}

.selected {
    border-color: #e13033; 
}


.element:hover {
    border-color: #e13033;
}

a {
    color: #e0f0ff;
    font-size: 1rem;
    text-decoration: none;
}

#apr {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
}

.container {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1024px;
    height: 80vh;
    align-items: center;
    justify-content: space-between;
    margin: 50px;
}

h1 {
    font-family: 'Oswald', 'Roboto', sans-serif;
    color: #fff;
    font-weight: bold;
    font-size: 5rem;
}

#profile {
    background-image: url(/portfolio/static/media/ProfileImage.2ffa8215.png);
    background-size: cover;
    width: 40vh;
    height: 40vh;
    border-radius: 40vh;
}

/*800px de largura*/

@media (max-width: 800px) {
    h1 {
        font-size: 4rem;
    }
}

/*700px de largura*/

@media (max-width: 700px) {
    h1 {
        font-size: 3rem;
    }
}

/*600px de largura*/

@media (max-width: 600px) {
    h1 {
        font-size: 2rem;
    }
    #profile {
        width: 20vh;
        height: 20vh;
    }
}
.about-container {
    width: 100%;
}

.image-container {
    width: 40vh;
    margin: 10vh auto;
}

.image-container img {
    width: 100%;
}


.skills-container {
    width: 100%;
}

.skills-container ul {
    display: flex;
    flex-direction: column;
}

.skills-container ul li {
    margin-top: 10px;
}
#projects {
    height: 100vh;
}
.contact-container {
    width: 100%;
}

.social-container {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.social-container .social-item {
    width: 50vh;
    margin-top: 50px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.social-container .social-item span, input {
    font-size: 1.5rem;
}

.social-container .social-item input {
    background-color: transparent;
    border: transparent;
    color: #dfdfea;
    cursor: pointer;
}

@media(max-width: 500px) {
    .social-container .social-item span, input {
        font-size: 1rem;
    }
    .social-container .social-item {
        width: 40vh;
        margin-top: 50px;
    }
}
